.window-container {
    display: flex;
    flex-direction: column;
    /* width: 90rem;
    height: 70rem; */
    background-color: var(--wheat);
    border: 3px solid black;
    border-radius: 15px;
    position: absolute;
    overflow: hidden;
    z-index: 100;
}
 @media (max-width:  400px) {
    .window-container {
        width: 66rem;
        height: 114rem;
    }
  }

.window-header {
    display: grid;
    grid-template-columns:  15fr 1fr 1fr;
    align-items: center;
    align-content: center;
    justify-items: center;
    height: 3.5rem;
    background-color: var(--dark-indigo);
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    border-bottom: 2px solid black;
    user-select: none;
    padding: 1rem;
}

.window-header h4 {
    width: 100%;
    height: 100%;
    text-align: center;
    user-select: none;
    font-size: 1.2rem;
    color: white;
    padding-top: 5px;
}

.window-content {
    text-align: center;
    overflow: auto;
}

.close-icon, .maximize-icon {
    width: 2rem;
    height: 2rem;
    filter: invert(100%);
}

::-webkit-scrollbar {
    width: 3rem;
}

::-webkit-scrollbar-track {
    background: var(--sienna);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border: 3px solid var(--dark-indigo);
  }

  ::-webkit-scrollbar-thumb {
    background-color: var(--golden);  /* Color del pulgar */
    border-radius: 8px;
    border: 3px solid var(--dark-indigo);  /* Espacio alrededor del pulgar */
    box-shadow: inset 0px -15px 2px -13px var(--dark-indigo);
  }

